import React, {Component} from 'react';
import {Col, Container, Row} from 'reactstrap'

import {BASE_URL} from '../../services/ApiService'
import "./HowToBuy.css";

class NormalHowtoBuyItem extends Component{

    render(){
        const {item} = this.props
        const { smallItem } = item
        return(
            <div className="HowtoBuyItem">
                <div className="HowtoBuyItemTitle">
                    {item.title}
                </div>
                <div className="HowtoBuyItemContent">
                    {Array.isArray(smallItem) && (
                        <>
                        {smallItem.map((sItem,index)=>{
                            console.log(sItem)
                            let txtItems = sItem.txtItem
                            let imgItems = sItem.imageItem
                            let colCnt = 0
                            if(txtItems.length != 0)
                                colCnt++
                            if(imgItems.length != 0)
                                colCnt++
                            return(
                                <div key={index} className="HowtoBuyItemSmall">
                                    <div className="HowtoBuyItemSmallTitle">
                                        {sItem.title}
                                    </div>
                                    <div className="HowtoBuyItemSmallContent">
                                        <Row>
                                            {Array.isArray(txtItems) && (
                                                <Col md={12/colCnt}>
                                                    <div className="HowtoBuySmallItemTextPart">
                                                    {txtItems.map((txt,tIndex)=>{
                                                        return(
                                                            <div key={tIndex} className="HowtoBuySmallItemTextItem">
                                                                <li>{txt.content}</li>
                                                            </div>
                                                        )
                                                    })}
                                                    </div>
                                                </Col>
                                            )}
                                            {Array.isArray(imgItems) && (
                                                <Col md={12/colCnt}>
                                                    <div className="HowtoBuySmallItemImagePart">
                                                    {imgItems.map((img,IIndex)=>{
                                                        return(
                                                            <div key={IIndex} className="HowtoBuySmallItemImgItem">
                                                                <img  src={`${BASE_URL}/upload/howto/${img.url}`}/>
                                                            </div>
                                                        )
                                                    })}
                                                    </div>
                                                </Col>
                                            )}
                                        </Row>
                                    </div>
                                </div>
                            )
                        })}
                        </>
                    )}
                </div>
            </div>
        )
    }
}
export default NormalHowtoBuyItem