import {
    SET_HOWTO_BUY
 } from './types'
import api from '../../services/ApiService.js'
export const getHowtobuy = () => dispatch=>{
    
    api.get('/howtobuy/',{

    })
    .then(res=>{
        dispatch({
            type:SET_HOWTO_BUY,
            payload:res.data
        })
    })
    
}
